import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    }, {
      path: '/home',
      name: 'home',
      component: resolve => require(['@/views/home.vue'], resolve)
    }, {
      path: '/detail',
      name: 'detail',
      component: resolve => require(['@/views/detail.vue'], resolve)
    },
    {path: '/', redirect: '/index'}
  ]
})



